header
{
    height: 100vh;
    padding-top: 7rem;
    overflow: hidden;
}

.header__container
{
    text-align: center;
    height: 100%;
    position: relative;
}

/* --------------cta--------------------- */
.cta
{
    margin-top: 2.5rem;
    justify-content: center;
    display: flex;
    gap: 1.2rem;
}

/* ------------social header------------- */
.header__socials
{
    display: flex;
    align-items: center;
    flex-direction: column;
    gap: 0.8rem;
    position: absolute;
    left: 0;
    bottom: 3rem;
    
}

/*-------------me----------------------  */

.me
{
    background: linear-gradient(var(--color-primary),transparent) ;
    width: 22rem;
    height: 30rem;
    position: absolute;
    left: calc(50% - 11rem);
    margin-top: 4rem;
    border-radius: 12rem 12rem 0 0;
    overflow: hidden;
    padding: 5rem 1.5rem 1.5rem ;

}


/* -----------------scroll ----------------------- */

.scroll__down
{
    position: absolute;
    right: -2.3rem;
    bottom: 5rem;
    transform: rotate(90deg);
    font-weight: 300;
    font-size: 0.9rem;
}


/* -------------media (medium devices)------------------- */
@media screen and (max-width:1024px) {
    .container
    {
        width: var(--container-width-md);
    }
    

    section
    {
        margin-top: 6rem;
    }
}

/* -------------media (small devices)------------------- */
@media screen and (max-width:600px) {
   .header
   {
    height: 100vh;
   }

   .header__socials,
   .scroll__down
   {
    display: none;
   }

}

/* -------------media (medium devices)------------------- */
@media screen and (max-width:1024px) {
    header
    {
        height: 68vh;
    }

}
